  const formatTime = (dateString) => {
    if (!dateString) return null;

    // Ensure the date string is correctly formatted
    const postDate = new Date(dateString);
    if (isNaN(postDate)) return "Invalid Date";

    // Adjust time by adding 1 hour (3600000 milliseconds)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Format using Intl.DateTimeFormat with Europe/Oslo timezone
    const options = {
      timeZone: "Europe/Oslo",
      day: "2-digit",
      month: "short",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      hour12: true, // Use AM/PM format
    };

    const formatter = new Intl.DateTimeFormat("en-US", options);
    return formatter.format(adjustedPostDate);
  };

