import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
import GDPRPopup from "./GDPRPopup";

export const Footer = () => {
  const { t } = useTranslation();
  const [signedup, setsignedup] = useState(false);
  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  const currentYear = new Date().getFullYear();

  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    if (userLogin !== null) {
      setsignedup(true);
    }
  }, []);
  return (
    <>
      <footer className="d-block">
        <div className="ftblocks py-5">
          <div className="container-lg">
            <div className="row gy-4">
              <div className="col-md-6">
                <div className="d-flex flex-column gap-3 ftlogoblock">
                  <h4>{t("WELCOMETOAMOURETTE")}</h4>
                  <div className="d-flex flex-column gap-1">
                    <h6>{t("Yourenow")}</h6>
                    <Link to="/helpsupport" className="needsign">
                      <span
                        dangerouslySetInnerHTML={{ __html: t("Needhelp") }}
                      />
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-md-3">
                <div className="d-flex flex-column gap-3 ftblock ps-md-5">
                  <h3>{t("About")}</h3>
                  <div className="d-flex flex-column gap-2 ftlink">
                    <Link to="/setting">{t("Membership")}</Link>
                    <Link to="/helpsupport">{t("Help_and_support")}</Link>
                    <Link to="/setting">{t("Contact_us")}</Link>
                  </div>
                </div>
              </div>
              <div className="col-md-3">
                <div className="d-flex flex-column gap-3 ftblock ps-md-5">
                  <h3>{t("Resources")}</h3>
                  <div className="d-flex flex-column gap-2 ftlink">
                    <Link to="/securitypage">{t("Security")}</Link>
                    <Link to="/setting">{t("privacy")}</Link>
                    <Link to="/helpsupport">{t("Terms_and_conditions")}</Link>
                  </div>
                </div>
              </div>
              <div className="col-12">
                <div className="d-block ftlogoblock">
                  <p className="copyrighttext">
                    &copy; {t("Copyright_2024_all_rights_reserved")}{" "}
                    {currentYear}, {t("all_rights_reserved")}
                  </p>
                </div>
              </div>
            </div>
            <GDPRPopup />
          </div>
        </div>
      </footer>
    </>
  );
};
